.forecast-preview {
    height: 100%;
    box-shadow: 0px 1px 4px rgba(0, 0, 0, 0.16);

    img {
        width: 150px;
    }
    .weather-description {
        text-align: center;
        padding: 5px 8px;
    }
    &.dark{
        box-shadow: 0px 1px 4px rgba(250, 250, 250, 0.16);
    }
}
